export const getSquarePosition = (board, square) => {
  for (let row = 0; row < board.length; row++) {
    for (let col = 0; col < board[row].length; col++) {
      if (board[row][col] === square) {
        return {
          row,
          col
        };
      }
    }
  }
  throw new Error('Square not found on the board.');
};